import { ColDef } from 'ag-grid-community'

import { columnDef } from '../DataGrid'
import { Taxon, MyUser } from '@namcbugdb/react-common'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

export const makeTranslationDetailColumnDefs = (user: MyUser): ColDef[] => {
  const colDefs = [
    columnDef.textColumn({
      headerName: 'OTU Name',
      field: 'translationScientificName',
      minWidth: 110,
      width: 300,
    }),
    columnDef.booleanColumn({
      headerName: 'Final',
      field: 'isFinal',
      width: 110,
      valueLabels: ['', 'Final'],
    }),
    {
      headerName: 'Taxa',
      field: 'taxa',
      minWidth: 110,
      flex: 1,
      cellRenderer: 'TaxaRenderer',
      comparator: ([a]: Taxon[], [b]: Taxon[]) => a.name > b.name,
    },
  ] as ColDef[]
  if (user.hasPermission(NAMCResource.translations, GrantAction.updateAny))
    colDefs.push({
      colId: 'actions',
      width: 120,
      cellRenderer: 'ActionsRenderer',
      pinned: 'right',
      sortable: false,
    })
  return colDefs
}
